.card {
  position: relative;
  margin-bottom: 24px;
  margin-top: 10px;
  background-color: #ffffff;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.33);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.33);
}

.card-topline-color-default {
  border-top: 3px solid var(--default-color);
}

.card-topline-yellow {
  border-top: 3px solid #f39c12;
}

.card-topline-aqua {
  border-top: 3px solid #00c0ef;
}

.card-topline-lightblue {
  border-top: 3px solid #3c8dbc;
}

.card-topline-green {
  border-top: 3px solid #00a65a;
}

.card-topline-red {
  border-top: 3px solid #dd4b39;
}

.card-topline-purple {
  border-top: 3px solid #ad5bff;
}

.card-topline-gray {
  border-top: 3px solid #ddd;
}

.card:before,
.card:after {
  content: ' ';
  display: table;
}

.card:after {
  clear: both;
}

.card > .nano:last-child {
  border-radius: 0 0 2px 2px;
}

.card.no-shadow {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.card-head {
  border-radius: 2px 2px 0 0;
  border-bottom: 1px dotted rgba(0, 0, 0, 0.2);
  padding: 2px;
  text-transform: uppercase;
  color: #3a405b;
  font-size: 14px;
  font-weight: bold;
  line-height: 40px;
  min-height: 40px;
}

.card-head:before,
.card-head:after {
  content: ' ';
  display: table;
}

.card-head:after {
  clear: both;
}

.card-head header {
  display: inline-block;
  padding: 11px 24px;
  vertical-align: middle;
  line-height: 17px;
  font-size: 16px;
  color: var(--default-color);
}

.card-head header > h1,
.card-head header > h2,
.card-head header > h3,
.card-head header > h4,
.card-head header > h5,
.card-head header > h6 {
  vertical-align: middle;
  margin: 0;
  line-height: 1;
}

.card-head header small {
  color: inherit;
  opacity: 0.6;
  font-size: 75%;
}

.card-head > div {
  display: inline-block;
}

.card-head .tools {
  padding-right: 16px;
  float: right;
  margin-top: 7px;
  margin-bottom: 7px;
  margin-left: 24px;
  line-height: normal;
  vertical-align: middle;
}

.card-head .tools .btn {
  border-radius: 2px;
  -webkit-box-shadow: none;
  box-shadow: none;
  border: 1px solid transparent;
  padding: 0px 2px;
  font-size: 12px;
  background: transparent;
}

.card-head .tools .btn-color {
  color: #97a0b3;
  margin-right: 3px;
  font-size: 12px;
}

.card-head .tools .btn-color:hover {
  color: var(--default-color);
}

.card-head.card-head-xs header {
  font-size: 15px;
}

.card-head.card-head-sm header {
  font-size: 15px;
}

.card-head.card-head-lg header {
  font-size: 24px;
}

.card-body {
  padding: 10px 24px 24px 24px;
  position: relative;
}

.card-body:before,
.card-body:after {
  content: ' ';
  display: table;
}

.card-body:after {
  clear: both;
}

.card-body:last-child {
  border-radius: 0 0 2px 2px;
}

.card-body.table-responsive {
  margin: 0;
}

